/*Scrollbar   */
/* --------- */

::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #3c4de6;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(91, 155, 228, 0.959);
  border-radius: 12px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(91, 155, 228, 0.959);
  border-radius: 12px;
}


